<template>
  <div class="login flex column ac">
    <div class="login-top">
      <h3>验证码登录</h3>
      <div class="form">
        <div class="form-item">
          <van-field
            v-model="username"
            maxlength="11"
            placeholder="手机号"
            type="tel"
            clearable
            :error-message="errorUser"
          />
        </div>
      </div>
      <van-button class="login-btn" type="default" color="#E84114" block round @click="onSubmit">登录</van-button>
      <div class="other text-center" @click="toAccount">密码登录</div>
    </div>
    <div class="btn flex column ac center">
      <van-divider
        :style="{ color: '#666', borderColor: '#d8d8d8', padding: '0 16px', width: '5rem' }"
      >其他登录方式</van-divider>
      <img :src="require('@/assets/wx2.png')" alt @click="wxAuth" />
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Divider, Field, Button } from "vant";
import { getVerificationCode, getWxCodeToAccess, wxLogin } from "@/api";
import { wxAuth } from "@/utils/wx";

Vue.use(Divider);
Vue.use(Field);
Vue.use(Button);

const tel = /^1[3-9]\d{9}$/;

export default {
  data() {
    return {
      username: "",
      errorUser: ""
    };
  },
  created() {
    this.wxLogin();
  },
  methods: {
    onSubmit() {
      if (this.username) {
        if (!tel.test(this.username)) {
          this.errorUser = "请输入正确的手机号";
          return;
        }
        this.errorUser = "";
        const params = {
          mobile: this.username,
          scene: 0
        };
        getVerificationCode(params).then(async res => {
          if (res.success) {
            this.$router.push(`/login/code?mobile=${this.username}`);
          }
        });
        return;
      }
      this.errorUser = this.username ? "" : "请输入手机号";
    },
    toAccount() {
      this.$router.replace("/login/account");
    },
    wxAuth() {
      const url = window.btoa(location.origin + this.$route.fullPath);
      wxAuth(url);
    },
    async wxLogin() {
      const { code } = this.$route.query;
      console.log("code：", code);
      if (code) {
        // 获取access-token
        const result = await getWxCodeToAccess({ code });
        if (result.success) {
          console.log("getWxCodeToAccess：", result);
          const { openid, unionid } = result.data;
          const params = {
            type: 2,
            openid,
            unionid,
            login_platform: 'h5'
          };
          // 微信登录
          const result2 = await wxLogin(params);
          console.log("wxLogin", result2);
          if (result2.success) {
            if (!result2.data.token) { // 未关联手机号
              this.$toast('该微信号未绑定相关账号，请使用手机号登录')
              setTimeout(() => {
                this.$router.replace('/login/account')
              }, 2000)
              return
            }
            localStorage.setItem("token", result2.data.token);
            localStorage.setItem("user_id", result2.data.user_id);
            localStorage.setItem("userInfo", JSON.stringify(result2.data));
            this.$router.replace("/home");
          }
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  height: 100%;
  background: #fff;
  overflow: hidden;
}

.login-top {
  width: 335px;
  margin-top: 40px;
  margin-bottom: 189px;
  h3 {
    color: #282828;
    font-size: 19px;
    line-height: 27px;
    margin: 0;
  }
}
.btn {
  width: 300px;
  margin-bottom: 59px;
  img {
    width: 48px;
  }
}

.form {
  margin-top: 10px;
  .van-cell {
    padding: 18px 0;
  }
}

.form-item {
  border-bottom: 1Px solid #dddedd;
  margin-bottom: 8px;
}

.login-btn {
  margin: 40px 0 15px;
}

.other {
  color: #666;
  font-size: 14px;
}
</style>
