import { config, ready, error } from 'tencent-wx-jssdk'
import { getSignPackage } from '@/api/index'

// 微信接口校验
export const jsApiListSignPackageAsync = (jsApiList, debug = false) =>
  // eslint-disable-next-line no-async-promise-executor
  new Promise(async (resolve, reject) => {
    const result = await getSignPackage() // 获取jssdk签名
    if (result) {
      const { success, data } = result
      if (success) {
        const { appId, timestamp, nonceStr, signature } = data;
        config({
          debug, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId, // 必填，公众号的唯一标识
          timestamp, // 必填，生成签名的时间戳
          nonceStr, // 必填，生成签名的随机串
          signature, // 必填，签名
          jsApiList
        })
        ready(() => resolve(data)) // 验证成功
        error((e) => reject(e)) // 验证失败
      }
    } else {
      reject(new Error('get signPackage error'))
    }
  })

// 微信授权
export const wxAuth = (callBackUrl) => {
  const defaultUrl = window.btoa(`${location.origin}/login`)
  const url = callBackUrl || defaultUrl
  location.href = `${process.env.VUE_APP_BASE_API}/Wx/Auth?auth_type=1&b=${url}`
}